/*FACES SQUARE*/
.bounding-box{
	position: absolute;
	box-shadow: 0 0 0 3px rgba(244,244,233,0.3) inset;
	display: -webkit-flex;
	display: flex;
	-webkit-flex-wrap: wrap;
	        flex-wrap: wrap;
	-webkit-justify-content: center;
	        justify-content: center;
	cursor: pointer;
}

.content-members{
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: center;
	        justify-content: center;
}

#members-image{
	position: absolute;
	margin-top: 100px;
}

.bounding-box:hover{
	box-shadow: 0 0 0 3px rgba(244,244,233,1) inset;
}

#inputImage{
	height: 80vh;
}

@media only screen and (max-width: 768px) {
	#members-image{ margin-left: 5%;  margin-top: 5%;}
}
@media only screen and (max-width: 600px) {
	#members-image{ margin-left: 0px;  margin-top: 45%;}
}


.modal-edge {
	color: rgba(213, 74, 74, 1);
	background-color: rgba(5, 0, 0, 0.8);
}

.modal-body{
	background-color: rgba(5, 0, 0, 0.6);
	color: white;
}
@-webkit-keyframes fadeInLeft {
  from {
    opacity: 0;
    -webkit-transform: translate3d(-100%, 0, 0);
    transform: translate3d(-100%, 0, 0);
  }

  to {
    opacity: 1;
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
  }
}

@keyframes fadeInLeft {
  from {
    opacity: 0;
    -webkit-transform: translate3d(-100%, 0, 0);
    transform: translate3d(-100%, 0, 0);
  }

  to {
    opacity: 1;
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
  }
}


/*CONTAINER BOX*/
.container-warning{
  width: 230px;
  margin-left: 30%;
  height: 400px;
  top: 100px;
  right: 50px;
  position: fixed;
  z-index: 1;
  -webkit-animation: fadeInLeft 3s linear;
          animation: fadeInLeft 3s linear;
}

/*THOUGHT BOX */
.thought{
   	background-color: rgba(244, 160, 83,1);
    border-radius: 15px;
    width: 200px;
    min-height: 100px;
    float: right;
    padding: 10px;
    font-style: italic;
    position: relative;
}

.thought p {
  font-size: 1.3em;
  text-align: center;
  margin-top: 15px;
}

.thought:after{
    content: "";
    width: 0;
    height: 0;
    position: absolute;
    border-left: 20px solid transparent;
    border-right: 20px solid transparent;
    border-top: 20px solid rgba(244, 160, 83,1);
    bottom: -20px;
    left: 20%;
}

/* GHOST */
.field {
  margin: 5% auto;
  display: table;
}

[class*="ghost"] {
  margin: 25px;
  height: 200px;
  width: 200px;
  position: relative;
}
[class*="ghost"] .head {
  border-top-left-radius: 100px;
  border-top-right-radius: 100px;
  border-bottom-left-radius: 0px;
  border-bottom-right-radius: 0px;
  height: 150px;
  width: 152px;
  background: rgba(213, 74, 74, 1);
  position: relative;
}
[class*="ghost"] .head .eye,
[class*="ghost"] .head .eyeBack {
  border-top-left-radius: 100%;
  border-top-right-radius: 100%;
  border-bottom-left-radius: 100%;
  border-bottom-right-radius: 100%;
  position: relative;
}
[class*="ghost"] .head .eyeBack {
  height: 50px;
  width: 50px;
  background: white;
}
[class*="ghost"] .head .eyeBack:first-child {
  margin: 50px 0 0 25px;
  float: left;
}
[class*="ghost"] .head .eyeBack:last-child {
  margin: 50px 25px 0 0;
  float: right;
}
[class*="ghost"] .head .eye {
  margin: 20% auto;
  height: 25px;
  width: 25px;
  background: black;
}
[class*="ghost"] .legs {
  height: 40px;
  width: 0;
  border-right: 38px solid rgba(213, 74, 74, 1);
  border-left: 38px solid rgba(213, 74, 74, 1);
  border-bottom: 40px solid transparent;
  display: block;
  float: left;
}

/*CLOSE BUTTON*/

[class*='close-'] {
  color: #777;
  font: 22px/100% arial, sans-serif;
  position: absolute;
  right: 3px;
  text-decoration: none;
  text-shadow: 0 1px 0 #fff;
  top: 3px;
  cursor: pointer
}

.close-thik:after {
  content: '\2716'; 
}

